.content{
      
    
    margin-top:4%;
    margin-bottom:5%;
    margin-left: auto;
    margin-right: auto;
    box-shadow: 0px 0px 3px white;
    padding: 2px;
    opacity: .95;
}

.login{
    opacity: .92;
    border-left: 1px solid #28a745;
    background-color: white;
}
.info-up{
    height: 40%;
    
    color: #28a745;
    
    
}
.card{
    padding: 0;
    margin: 0;
}
.info-down{
    height: 60%;
    padding: 20px;
        
}
.info-left{
    opacity: .92;
    background-color: white;
    
}
.space-top{
    width: 100%;
    
}


