.forgot{
    padding-top: 15%;
    padding-bottom: 5%;
    padding-right: 3%;
    padding-left: 3%;
       
}




.user-img{
    margin-top: 10px;
    margin-bottom: 10px;
}
.user-img img{
    width: 150px;
    height: 150px;
    box-shadow: 0px 0px 3px #28a745;
    border-radius: 50%;
    
}


.form-group input{
    height: 42px;
    font-size: 18px;
    border-color: #28a745;
    padding-left: 54px;
    border-radius: 5px;
}

.form-group::before{
    font-family: "Font Awesome\ 5 Free";
    position: absolute;
    left: 28px;
    font-size: 22px;
    padding-top:4px;
}

